.employee {
    border: 4px solid black;
    width: 200px;
    height: 300px;
    padding: 14px;
    margin: 0 auto;
    background-color: lavender;
    border: solid black 2px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 0 1.5px 0px black;
    display: inline-block;
    
}

.list {
    margin: 10px 10px;
    padding: 10px;
}
.employee:hover{ 
    transform: scale(1.2);
    transition: all 0.5s;
}